<template>
<div class="userInfo">
  <h2>请填写您的个人信息</h2>
  <h4>（仅用于研究，带<span>*</span>为必填项）</h4>
  <el-form ref="form" :model="form" :rules="rules">
    <el-form-item label="姓名" prop="name">
      <el-input v-model="form.name" placeholder="请填写"></el-input>
    </el-form-item>
    <el-form-item label="性别" prop="sex">
      <el-select v-model="form.sex" placeholder="请选择">
        <el-option v-for="(sex,index) in options.sexs" :key="index" :label="sex" :value="index+1">
        </el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="年龄" prop="age">
      <el-select v-model="form.age" placeholder="请选择">
        <el-option v-for="(age,index) in options.ages" :key="index" :label="age" :value="age">
        </el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="籍贯" prop="place">
      <el-select v-model="form.place" placeholder="请选择">
        <el-option v-for="(place,index) in options.places" :key="index" :label="place" :value="place">
        </el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="工作岗位">
      <el-input v-model="form.work" placeholder="请填写"></el-input>
    </el-form-item>
  </el-form>
  <div class="nextstep" @click="nextstep('form')">
    下一步
  </div>
</div>
</template>

<script>
import {
  Popup
} from 'mint-ui';
import {
  addQuestionnaireUser
} from 'api/question'
export default {
  name: 'userInfo', //介绍页面
  data: function() {
    return {
      form: {
        name: '',
        sex: '',
        age: '',
        place: '',
        work: '',
      },
      options: {
        sexs: ['男', '女', '保密'],
        ages: ['18~22', '22~26', '26~30', '30~35', '35以上'],
        places: ["北京", "天津", "河北", "山西", "内蒙古", "辽宁", "吉林", "黑龙江", "上海", "江苏", "浙江", "安徽", "福建", "江西", "山东", "河南", "湖北", "湖南", "广东", "广西", "海南", "重庆", "四川", "贵州", "云南", "西藏", "陕西", "甘肃", "青海", "宁夏", "新疆", "香港", "澳门"]
      },
      rules: {
        name: [{
          required: true,
          message: '请输入姓名',
          trigger: 'blur'
        }],
        sex: [{
          required: true,
          type: 'number',
          message: '请选择性别',
          trigger: 'change'
        }],
        age: [{
          required: true,
          message: '请选择年龄范围',
          trigger: 'change'
        }],
        place: [{
          required: true,
          message: '请选择籍贯',
          trigger: 'change'
        }]
      }
    }
  },
  methods: {
    ceshi() {
      console.log(this.form)
    },
    nextstep(formName) {
      //判断是否填入的为空
      this.$refs[formName].validate((valid) => {
        if (valid) {
          //数据提交到后端
          let obj = {
            userName: this.form.name,
            sex: this.form.sex,
            age: this.form.age,
            address: this.form.place,
            post: this.form.work
          }
            this.$router.replace('firstpart')

            
          // addQuestionnaireUser(obj).then(res => {
          //   if (res.data.Result == 'success') {
          //     this.$router.replace('firstpart')
          //   }
          // })
        } else {
          return false;
        }
      });
    },
  },
  created() {

  }
}
</script>
<style scrope lang="less">
@import '../../../styles/mixin.less';
@rem: 30rem;
.el-input__inner {
  height: 60/@rem;
}
</style>

<style scrope lang="less">
@import '../../../styles/mixin.less';
.userInfo {
  overflow: hidden;
  h2 {
    #wh(100%, 60/@rem);
    margin-top: 54/@rem;
    #sc(42/@rem, #333333);
    line-height: 60/@rem;
    text-align: center;
  }
  h4 {
    #wh(100%, 48/@rem);
    #sc(30/@rem, #333333);
    line-height: 48/@rem;
    text-align: center;
    font-weight: normal;
    span {
      color: red;
    }
  }
  .el-form {
    padding: 0 42/@rem;
    .el-form-item.is-required {
      margin-bottom: 10/@rem;
    }
    .el-form-item__label {
      display: block;
      height: 48/@rem;
      line-height: 48/@rem;
      float: none;
      text-align: left;
      #sc(28/@rem, #333333);
    }
    .el-form-item.is-required .el-form-item__label:before {
      color: red;
    }
    .el-form-item__content {
      .el-input__inner {
        border-radius: 0;
      }
    }
    .el-select {
      width: 100%;
    }
    .el-form-item__error {
      top: -40/@rem;
      font-size: 28/@rem;
      right: 0;
      left: auto;
    }
  }
  .nextstep {
    position: fixed;
    bottom: 0;
    #wh(100%, 100/@rem);
    text-align: center;
    #sc(30/@rem, #fff);
    line-height: 100/@rem;
    background-image: linear-gradient(-174deg, #04A2FC 0%, #401CD5 100%);
  }
}
</style>


